<template>
	<view class="monitor-page">
		<view class="addheader">
			<view class="addheaderbac"></view>
			<view class="device-tip">
				<view class="devicenum">
					<text>{{deviceAll}}</text>
					设备总数
				</view>
				<view class="addline"></view>
				<view class="devicenum">
					<text>{{normalHost}}</text>
					在线设备
				</view>
				<view class="addline"></view>
				<view class="devicenum">
					<text>{{abnormalHost}}</text>
					离线设备
				</view>
			</view>
		</view>
		<view class="page-body">
			<!-- 搜索 -->
			<view class="search-store">
				<input type="text" placeholder="输入门店名称进行搜索" />
				<view class="square"><icon type="search" size="16" color="#ffffff" /></view>
			</view>
			<!-- 下拉选择集团 -->
			<view class="check_cyList">
				<view class="clearfix">
					<view class="selectcheck" @click="dropcyList">
						<image src="../../static/image/address.png" mode=""></image>
						{{cyList[slectcyIndex].title}}
						<uni-icons :type="dropCylistShow?'arrowup':'arrowdown'" color="#333"></uni-icons>
					</view>
					<view class="square rightcheck" @click="dropcyList">
						<image src="../../static/image/scaner.png" mode=""></image>
					</view>
				</view>
				<uni-list class="checklist" v-if="dropCylistShow">
					<uni-list-item v-for="(item,index) in cyList" :key="index" :title="item.title" clickable  @click="selectCy(index)">
						<template slot="footer" v-if="index == slectcyIndex">
							<uni-icons type="checkmarkempty" color="#266CEB"></uni-icons>
						</template>
					</uni-list-item>
				</uni-list>
			</view>
			<!-- 门店状态分类 -->
			<view class="shop_type">
				<view :class="[{select_type:selectTab=='all'},'reqa']" type="all">全部</view>
				<view class="addceline"></view>
				<view :class="[{select_type:selectTab=='online'},'reqa']" type="online">在线</view>
				<view class="addceline"></view>
				<view :class="[{select_type:selectTab=='offline'},'reqa']" type="offline">离线</view>
			</view>
		</view>
	</view>
</template>

<script>
	import uniList from '@/components/uni-list/uni-list.vue'
	import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
	export default {
		components: {
			uniList,
			uniListItem
		},
		data() {
			return {
				deviceAll: "238",
				normalHost: '138',
				abnormalHost: '100',
				searchStore: '',
				addresIcon: '../../static/image/address.png',
				dropCylistShow:false,
				slectcyIndex:0,  //选中下拉集团下标
				cyList:[
					{
						"title":"中石化加油站"
					},
					{
						"title":"集团1"
					},
					{
						"title":"集团2"
					},
					{
						"title":"集团3"
					},
				],
				selectTab:'all'  //选中门店列表状态类型
			}
		},
		methods:{
			dropcyList(){
				this.$data.dropCylistShow=!this.$data.dropCylistShow
			},
			selectCy(index){
				this.$data.slectcyIndex=index;
				this.$data.dropCylistShow=false;
			}
			
		},
		onLoad() {
		},
		onShow() {

		}
	}
</script>

<style lang="scss">
	.addheader {
		width: 100%;
		height: 186rpx;
		position: relative;
	}

	.addheaderbac {
		width: 100%;
		height: 122rpx;
		background: #266CEB;
	}

	.device-tip {
		width: calc(100% - 60rpx);
		height: 140rpx;
		background: #FFFFFF;
		border: 1rpx solid #FFFFFF;
		box-shadow: 0px 0px 35rpx 0px rgba(72, 108, 224, 0.13);
		border-radius: 20rpx;
		display: flex;
		align-items: center;
		position: absolute;
		bottom: 0;
		left: 30rpx;
	}

	.devicenum {
		flex: 1;
		text-align: center;
		color: #999999;
		font-size: 24rpx;
	}

	.devicenum text {
		display: block;
		color: #266CEB;
		font-size: 40rpx;
		font-weight: 600;
		margin-bottom: 10rpx;
	}

	.device-tip .addline {
		width: 1rpx;
		height: 44rpx;
		background: #000000;
		opacity: 0.1;
	}

	.page-body {
		padding: 0 30rpx;
	}

	.search-store {
		margin: 41rpx 0;
		clear: both;
		overflow: hidden;
		input {
			width: 602rpx;
			height: 68rpx;
			background: #F0F5F9;
			box-shadow: 0px 13rpx 13rpx 0rpx rgba(240, 245, 249, 0.23);
			border-radius: 10rpx;
			padding: 0 20rpx;
			font-size: 28rpx;
			color: #8FA4B7;
			box-sizing: border-box;
			float: left;
		}
		view {
			display: flex;
			float: left;
			background: #33CE55;
			margin-left: 20rpx;
			uni-icon {
				font-size: inherit;
			}
		}
		.uni-collapse-cell__title-img{
			width: 28rpx;
			height: 34rpx;
		}
	}
	.check_cyList{
		position: relative;
		.selectcheck{
			float: left;
			font-size: 36rpx;
			color: #333333;
			font-weight: 500;
			image{
				width:28rpx;
				height: 34rpx;
				margin:5rpx 10rpx 0 0;
			}
			.uni-icons{
				margin-left: 10rpx;
			}
		}
		.rightcheck{
			background:#F0F5F9;
			float: right;
			image{
				width: 38rpx;
				height: 38rpx;
			}
			
		}
		/deep/ .uni-collapse-cell__title{
			padding: 12px 0;
			.uni-collapse-cell__title-img{
				width:28rpx;
				height: 34rpx;
				margin-right: 10rpx;
			}
		} 
		.checklist{
			margin-top: 20rpx;
			position: absolute;
			width: 100%;
			z-index: 2;
			height: 336rpx;
			overflow-y: auto;
		}
	}
	.shop_type{
		width: 100%;
		
		background: #F0F5F9;
		border-radius: 10rpx;
		margin-top: 30rpx;
		display: flex;
		align-items: center;
		view:first-child{
			border-top-left-radius: 10rpx;
			border-bottom-left-radius: 10rpx;
		}
		view:last-child{
			border-top-right-radius: 10rpx;
			border-bottom-right-radius: 10rpx;
		}
		.reqa{
			flex: 1;
			height: 78rpx;
			font-size: 32rpx;
			color: #8FA4B7;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.addceline{
			width: 2rpx;
			height: 78rpx;
			background: #FFFFFF;
		}
		.select_type.reqa{
			background: #266CEB;
			color: #FFFFFF;
		}
	}
	
</style>
